$font: 'Roboto', Arial,Helvetica,sans-serif;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #fff;
  background: url("/dist/img/bg.png") no-repeat;
  background-size: cover;
  font: 400 14px/14px $font;
}

p {
  margin: 0;
  padding: 0;
}

a,
a:hover {
  text-decoration: none;
}

.main {
  position: relative;

  .header {
    padding: 50px;
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;

    a {
      margin: 0 20px;
      color: #fff;
      font: 400 15px $font;
    }
  }
}
.page_about {
  color: #fff;
  width: 1000px;
  margin:0 auto;
  padding-top: 150px;
  height: 100vh;
  font: 400 20px/1.4em $font;
  letter-spacing: 0.5px;
    text-shadow: 1px 1px 2px black, 0 0 1em black;
    h2 {
      font:400 50px/1.4em $font;
      text-align: center;
      margin: 20px;
    }
}
.page_main {
  display: flex;
  align-items: center;
  justify-content: center;
  font: 400 66px/1.4em $font;
  letter-spacing: 0.5px;
    text-shadow: 1px 1px 2px black, 0 0 1em black;
  color: #fff;
  height: 100vh;

  p {
    margin: 0 auto;
    text-align: center;
    width: 1000px;
  }
}
#addr {
  background: #fff;
  padding: 20px 0;
  display: flex;
  justify-content: flex-end;
  .addr {
    width: 400px;
      font: 400 16px/1.4em $font;
    padding: 50px;
      color: #000;

  }

}
.footer {
  background: #000;
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
    font: 400 15px/1.4em $font;
  color:#fff;
}
